:root {
    --background: oklch(var(--n));
    --accent: oklch(var(--p));
  }
  ::-webkit-scrollbar {
    height: 10px;
    width: 10px;
    background: #fff3;
    -webkit-border-radius: 1ex;
  }
  ::-webkit-scrollbar-thumb {
    background: var(--accent);
    -webkit-border-radius: 1ex;
  }  
  ::-webkit-scrollbar-corner {
    background: #fff3;
  }
  
  .out{
    display: flex;
    list-style: none;
    overflow-x: scroll;
    padding: 20px 0;
    margin: 0 auto;
    }