.hoverText {
    transition:
      0.35s font-weight,
      0.35s color;
  }
  
  .hoverText:hover {
    color: rgb(238, 242, 255);
  }
  
  .hoverText:hover + .hoverText {
    color: rgb(142, 143, 148);
  }
  
  /* To the left */
  .hoverText:has(+ .hoverText:hover) {
    color: rgb(142, 143, 148);
  }
  