
*{
  /*border: 1px dashed #777;*/
  transition: .2s;
  scroll-behavior: smooth;
  filter: drop-shadow(0 1px 1px rgb(0 0 0 / 0.05));
}

body {
  overflow-x: hidden; /* Hide scrollbars */
}
