img {
  border-radius: 6px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.div-nav{
  border-radius: 6px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.div-divider{
  position:relative;
  right:0;
  width:80%;
  height:3px;
}

.bd{
border:2px dashed #555;
}

.hover-ani:hover{
transform:scale(1.2);
z-index:35;
}

.hover-flip:hover{
  animation: flip ease-in-out 0.4s 1;
}

@keyframes flip{
  0%{
    transform:scaleY(100%);
  }
  50%{
    transform:scaleY(-100%);
  }
  100%{
    transform:scaleY( 100%);
  }
}

.progress-bar {
  height: 200px;
  width:200px;
  background: #AAA;
  transform-origin: 0%;
  border-radius:20px;
}

.box{
  width:500px;
  height:500px;
  background:#DDD;
}